    position: fixed;
    bottom: 15rem;
    right: 0;
    width: 30rem;
    z-index: 60000000;
    background: #003764!important;

    padding: 1.5rem;

    color: #fff;
    transform: all .5s ease;

    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-style: normal;
}

@media only screen and (max-width: 1024px) {
    .chat {
        width: 100vw;
        bottom: 0;

        transform: translateY(22rem);
    }

    .chat.open {
        transform: translateY(0);
    }

    span.direction {

    }

    span.direction svg {
        height: 1rem;
        width: 1rem;
        fill: #000;
        color: red
    }
}

.chat__body--header {
    margin-bottom: 2rem;
}

.chat__body--input {
    margin-bottom: 1rem;
}

.chat__body--input input, textarea {
    width: 100%;
    height: 3rem;
    outline: none;
    padding: 1rem;
    border: none;
    background: #1E2221;
    font-size: 1rem;
    color: #fff;
}

.chat__body--input textarea {
    height: 9rem;
}

.chat__body--subscribe {
    font-size: 1.3rem;
    font-weight: 600;
}

.chat__body--subscribem {
    display: none
}

.chat__body--signals {
    font-size: 1.1rem;
    font-weight: 500;
}

.chat__body--h3sub {
    font-size: 1rem;
    opacity: .7;
}

.chat__body--subscibebtn button {
    outline: none;
    cursor: pointer;
    background: #1074b7;
    color: #fff;
    border: none;
    padding: 1rem;
    border-radius: .4rem;
    transition: all .5s ease;
}

.chat__body--subscibebtn button:hover {
    background: rgba(#1074b7, .8);
}

.alertbox {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    transition: all .5s ease;
    z-index: 100000000;
    height: 5rem;
}

.alertbox__content {
    margin: 0 auto;
    padding: 1rem;
    line-height: 1rem;
    background: #1074b7;
    text-align: center;
    color: #fff;
    font-size: 1rem;
}


@media only screen and (max-width: 1024px) {
    .chat__body--subscribem {
        display: flex;
        font-size: 1rem;
        font-weight: 600;
        background: #fff;
        width: 8rem;
        color: #1074b7;
        padding: 1rem;
        border-radius: .5rem;
        text-align: center;
        align-items: center;
    }

    .chat__body--subscribe {
        display: none
    }
    
    .chat__body--signals {
        font-size: 1rem;
        font-weight: 500;
    }
    
    .chat__body--h3sub {
        font-size: 1rem;
    }
}
